*{

  margin :0;
  padding: 0;
  box-sizing: border-box;
  
}
body{
  font-family: sans-serif;
}

.app
{
  background-image: url('./assets/cold-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition:0.5 ease-out;
  
}

.app.warm
{
  background-image: url('./assets/warm-bg.jpg');
  background-size: cover;
  background-position: bottom;
  transition:0.5 ease-out;
  
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  padding: 25px;
}
.search-box
{
  width:100%;
  margin:0 auto 75px;
}
.search-box .search-bar
{
  display: block;
  width: 100%;
  padding: 15px;
  background:none;
  appearance: none;
  border:none;
  outline: none;
  background-color: rgba(255,255,255,0.5);
  border-radius: 0 0 16px 16px;
  color:#313131;
  font-size: 15px;
  transition: 0.45 ease;
  margin-top: -25px;

  box-shadow:0 px 5px rgba(255,255,255,0.5);
}

.search-box .search-bar:focus
{
  background-color: rgba(255,255,255,0.75);
}


.location-box .location{
color:  #fff;
font-size:32px;
font-weight:500;
text-align: center;
text-shadow:3px 3px rgba(110, 110, 110, 0.5);
}

.location-box .date
{
  color:  #fff;
font-size:20px;
font-weight:300;
text-align: center;
text-shadow:3px 3px rgba(110, 110, 110, 0.5);
}

.weather-box
{

  text-align: center;
}

.weather-box .temp
{
  position:relative;
  display: inline-block;
  margin : 30px;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color:white;
  font-size: 80px;
  font-weight:600;
  text-shadow:3px 3px rgba(110, 110, 110, 0.5);
  text-align: center;
  font-family: sans-serif;
  box-shadow: 3px 6px rgba(0,0,0,0.2) ;
}
.weather-box .weather{
  padding: 15px 25px;
  color:white;
  font-size: 40px;
  font-weight:600;
  text-align: center;
  text-shadow:3px 3px rgba(34, 34, 34, 0.5);
}